import {
  ComponentProps,
  withStreamlitConnection,
  StreamlitComponentBase,
  Streamlit,
} from 'streamlit-component-lib'
import React, { ReactNode } from 'react'

import styles from './style.module.scss';

// Define here the available hosts
enum Platform {
  sketchup,
  rhino,
  revit
}

interface State {
  geometry: string
  identifier: string
  isPollinationModel: boolean
  isFocused: boolean
  syncToken: string
  platform: string
}

declare global {
  interface Window {
    chrome: any;
    sketchup: any;
  }
}

class GetGeometry extends StreamlitComponentBase<State> {

  public constructor(props: ComponentProps) {
    super(props)

    // Values from user input
    const isPollinationModel = this.props.args['isPollinationModel'] as boolean;
    const platform = this.props.args['platform'] as string;
    // const syncToken = this.props.args['syncToken'] as string

    const initialState = { 
      geometry: '', 
      identifier: '',
      isPollinationModel: isPollinationModel,
      platform: platform.toLowerCase()
    } as State

    this.state = {
      ...initialState
    }
  }

  public render = (): ReactNode => {

    const imgStyle = {
      margin: '0px 3px 2px 0px'
    }

    return (
      <span>
        <button
          className={styles.poButton}
          onClick={this._onClicked}
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}
        >
          <span>
            <img style={imgStyle} 
            src={process.env.PUBLIC_URL + '/img/pollination.png'} 
            alt='logo' />
          </span>
          Get { this.state.isPollinationModel ? 
            'Pollination Model' : 
            'Geometry' } 
            { Boolean(this.props.args['syncToken']) && 
            ' (Sync)' }
        </button>
      </span>
    )
  }

  componentDidUpdate() {
    // Empty identifier is not allowed
    if (this.state.identifier === '')
      return;
    
    // Same synctoken is not allowed
    if (this.props.args['syncToken'] 
      === this.state.syncToken )
      return;

    // Try get array GUIDs
    const stateIdentifiers = JSON
      .parse(this.state.identifier) as Array<string>;

    // Empty GUIDs
    if (!stateIdentifiers)
      return;
    
    // Try get the key GUID as trigger
    const id = this.props.args['syncToken']
      .split(':', 1)[0];
    
    if (stateIdentifiers.includes(id)
      && this.state.syncToken 
      !== this.props.args['syncToken'])
    {
      let newState = {} as State;
      if (this.state.platform == Platform[Platform.rhino])
      {
        newState = this._dotNetRun();
      }
      this._setStateNow(newState);
      console.log('[POLLINATION-DEBUG]: auto-update.');
    }
  }

  private _setStateNow = (newState: State): 
    void => {
    this.setState(
      (prevState) => ({ 
        ...prevState, 
        geometry: newState.geometry,
        identifier: newState.identifier,
        isPollinationModel: newState.isPollinationModel,
        syncToken: newState.syncToken,
        platform: newState.platform
      }),
      () => Streamlit.setComponentValue(this.state.geometry)
    );
    console.log('[POLLINATION-DEBUG]: JSON string sent.');
  }

  private _rubyRun = (): State => {
    if (typeof window.parent.sketchup != 'undefined') 
    {
      // ruby invoke
      let message = null;
      if (this.props.args['isPollinationModel']) {
        window.parent.sketchup.get_model();
        message = window.parent.sketchup.hb_model;
      } else {
        // TODO: pass selection
        window.parent.sketchup.get_geometry();
        message = window.parent.sketchup.geometries;
      }

      if (message == null)
        return this.state;

      console.log(message)
      const platform = this.props.args['platform'] as string;

      var newState = {
        geometry: message.Geometry, // model json
        identifier: message.Identifier, // model id
        isPollinationModel: this.props.args['isPollinationModel'],
        syncToken: this.props.args['syncToken'],
        platform: platform.toLowerCase()
      } as State;
  
      return newState;
    }
    else {
      console.log('[POLLINATION-DEBUG]: sketchup not found.');
      return this.state;
    }
  }

  private _dotNetRun = (selection: boolean = false): State => {
    if (typeof window.parent.chrome != 'undefined') {
      if (typeof window.parent.chrome.webview == 'undefined')
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return this.state;
      }

      // DotNet invoke
      let message = null;
      if (!this.props.args['isPollinationModel']) {
        message = window.parent.chrome.webview
        .hostObjects.sync.geometry.PassJsonGeometry(
          this.props.args['key'], 
          selection);
      } else {
        message = window.parent.chrome.webview
        .hostObjects.sync.hbjson.PassPollinationModel(
          selection);
      }
      if (message == null)
        return this.state;

      const platform = this.props.args['platform'] as string;
  
      var newState = {
        geometry: message.Geometry,
        identifier: message.Identifier,
        isPollinationModel: this.props.args['isPollinationModel'],
        syncToken: this.props.args['syncToken'],
        platform: platform.toLowerCase()
      } as State;
  
      return newState;
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
      return this.state;
    }
  }

  private _onClicked = (): void => {
    // Run with selection enabled
    // Geometries, list of GUIDs saved on runtime
    // Pollination Model, Model GUID
    const platform = this.props.args['platform'] as string;
    let newState = { platform: platform.toLowerCase() } as State;
    if (platform.toLowerCase() == Platform[Platform.rhino]
      ) { 
      newState = this._dotNetRun(true); 
    } else if (platform.toLowerCase() == Platform[Platform.sketchup]) {
      newState = this._rubyRun(); 
    }
    this._setStateNow(newState);
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(GetGeometry)
